<template>
  <div class="loginmima" v-wechat-title="$route.meta.title">
    <div class="login-box">
      <div class="box">
        <div class="title">登录</div>
        <div class="form">
          <div class="faster">
            <!--<router-link to="loginphone">手机快捷登录</router-link>-->
          </div>
          <el-input v-model="phone" placeholder="手机号"></el-input>
          <el-input class="password" placeholder="密码" v-on:keyup.enter="tologin" v-model="password"
                    show-password></el-input>
        </div>
        <div class="forget">
          <div class="agree">登录即表示你同意 用户协议 和 隐私条款</div>
          <!--<router-link to="#">忘记密码</router-link>-->
        </div>
        <el-button type="primary" @click="tologin" class="login">登录</el-button>
        <router-link  class="register" to="register">注册</router-link>
        <!--  <div class="other">
            <img class="weixin" src="@/images/weixin.png" alt="">
            <img class="weibo" src="@/images/weibo.png" alt="">
            <img class="qq" src="@/images/qq.png" alt="">
          </div>-->
      </div>
    </div>
  </div>
</template>

<script>
  export default {
    name: "loginmima",
    data() {
      return {
        phone: '',
        password: ''
      }
    },
    mounted() {
    },
    methods: {
      //登录
      tologin() {
        var reg = /^(13[0-9]|15[012356789]|17[0-9]|18[0-9]|14[57])[0-9]{8}$/;
        if (!this.phone) {
          this.$message("请输入手机号码");
        } else if (!reg.test(this.phone)) {
          this.$message("请填写有效的手机号！");
        } else if (this.password === "") {
          this.$message("请填写账号密码");
          return false;
        } else {
          this.$post("index/data/login", {
            phone: this.phone,
            password: this.password
          }).then(res => {
            // console.log(result.data);
            // console.log(334364);
            // console.log(typeof (result.data));
            // console.log("'" + result.data + "'");
            // let res = JSON.parse("'"+result.data+"'");
            // console.log(res);
            if (res.data.result === 200) {
              this.$message({
                message: '登录成功',
                type: "success"
              });
              let userinfo = res.data.user;
              userinfo.head = this.$httpUrl + "/uc_server/avatar.php?uid=" + userinfo.uid + "&amp;size=big";
              this.$store.commit("setLogin", userinfo); //登录状态保存在$store
              setTimeout(() => {
                if (this.$route.query.redirect) {
                  this.$router.push(this.$route.query.redirect);
                } else {
                  this.$router.push("/");
                }
              }, 1000);
            } else {
              this.$message.error(res.data.msg);
            }
          });
        }
      }
    }
  }
</script>

<style scoped lang="scss">
  $main: #409eff !default;
  .loginmima {
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    background: url('~@/images/bg.png') center center no-repeat;

    .login-box {
      width: 734px;
      height: 463px;
      margin: 0 auto;
      background: rgba(255, 255, 255, 1);
      box-shadow: 0px 3px 12px 0px rgba(0, 0, 0, 0.1);
      border-radius: 6px;
      display: flex;
      flex-direction: column;

      .box {
        width: 300px;
        margin: 0 auto;

        .title {
          margin: 36px 0;
          width: 100%;
          text-align: center;
          font-size: 30px;
          font-weight: 400;
          color: rgba(51, 51, 51, 1);
          line-height: 34px;
        }

        .form {

          .faster {
            display: flex;
            justify-content: flex-end;

            a {
              color: $main;
              text-align: right;
              font-size: 14px;
              font-weight: 400;
              line-height: 34px;
            }
          }

          .password {
            margin-top: 12px;
          }


        }

        .register {
          display: flex;
          justify-content: center;
          cursor: pointer;
          margin-top: 13px;
          font-size: 16px;
          font-weight: 400;
          color: rgba(153, 153, 153, 1);
          line-height: 34px;
          span {
            font-size: 16px;
            font-weight: 400;
            color: rgba(153, 153, 153, 1);
            line-height: 34px;
          }
        }

        .forget {
          width: 100%;
          margin-top: 10px;
          display: flex;
          justify-content: space-between;

          .agree {
            font-size: 12px;
            font-weight: 400;
            color: rgba(102, 102, 102, 1);
            line-height: 34px;
          }

          a {
            font-size: 12px;
            font-weight: 400;
            color: $main;
            line-height: 34px;
          }
        }

        .login {
          font-size: 16px;
          width: 100%;
          margin-top: 30px;
        }

        .other {
          margin-top: 38px;
          display: flex;
          justify-content: space-between;

          img {
            height: 39px;
          }

          .weixin, .weibo {
            width: 39px;
          }

          .qq {
            width: 30px;
          }
        }
      }


    }
  }
</style>



